import React from "react";
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import { MarkerIcon } from './Icons';

import 'leaflet/dist/leaflet.css';

const LeafletMap = ({ lat, lon, zoom, popupText, mapHeight, onMarkerClick, onpupClick }) => {
    const mapStyles = {
        width: "100%",
        height: mapHeight || 400, // default map heigth
    };
    return (
        <MapContainer center={{ lat: lat || 51.505, lon: lon || -0.09 }} zoom={zoom || 12} scrollWheelZoom={false} style={mapStyles}>
            <TileLayer
                attribution=''
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            //url="https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_all/{z}/{x}/{y}.png"
            />
            <Marker position={[lat || 51.505, lon || -0.09]} icon={MarkerIcon}
                eventHandlers={{
                    click: onMarkerClick || (() => { })
                }}
            >
                <Popup>
                    <span onClick={onpupClick || (() => { })}>{popupText}</span>
                </Popup>
            </Marker>
        </MapContainer>

    )
}

export default LeafletMap;