import PropTypes from "prop-types";
import React from "react";
import LeafletMap from "../leaflet-map/LeafletMap";

const SectionTitleWithText = ({ spaceTopClass, spaceBottomClass, shop }) => {
  return (
    <div
      className={`welcome-area ${spaceTopClass ? spaceTopClass : ""} ${spaceBottomClass ? spaceBottomClass : ""
        }`}
    >
      <div className="container">
        <div className="welcome-content text-center">
          <h5>
            {shop.city} {shop.state}
          </h5>
          <h1>{shop.name}</h1>
          <div className="col-lg-8 offset-lg-2 col-md-10 offset-md-1 col-12 text-center">
            <h5 className="pb-5">{shop.description}</h5>
          </div>
          <div className="custom-row-2">
            <div className="col-lg-5 col-12">
              <div className="contact-info-wrap p-lg-4 p-2 pt-5 text-justify">
                <div className="single-contact-info">
                  <div className="contact-icon">
                    <i className="fa fa-phone" />
                  </div>
                  <div className="contact-info-dec">
                    <p>{shop.phone}</p>
                  </div>
                </div>
                <div className="single-contact-info">
                  <div className="contact-icon">
                    <i className="fa fa-globe" />
                  </div>
                  <div className="contact-info-dec">
                    <p>
                      <a href={shop.url}>{shop.url}</a>
                    </p>
                  </div>
                </div>
                <div className="single-contact-info">
                  <div className="contact-icon">
                    <i className="fa fa-map-marker" />
                  </div>
                  <div className="contact-info-dec">
                    <p>{shop.street} </p>
                  </div>
                </div>
                <div className="single-contact-info align-items-start">
                  <div className="contact-icon">
                    <i className="fa fa-bicycle my-start" />
                  </div>
                  <div className="contact-info-dec">
                    <p className="w-100 ">{shop.brandsCarried} </p>
                  </div>
                </div>
                <div className="contact-social text-center">
                  <h3>Follow Us</h3>
                  <ul>
                    <li>
                      <a href="//facebook.com">
                        <i className="fa fa-facebook" />
                      </a>
                    </li>
                    <li>
                      <a href="//pinterest.com">
                        <i className="fa fa-pinterest-p" />
                      </a>
                    </li>
                    <li>
                      <a href="//thumblr.com">
                        <i className="fa fa-tumblr" />
                      </a>
                    </li>
                    <li>
                      <a href="//vimeo.com">
                        <i className="fa fa-vimeo" />
                      </a>
                    </li>
                    <li>
                      <a href="//twitter.com">
                        <i className="fa fa-twitter" />
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-lg-7 col-12">
              <div className="d-block d-lg-none">
                <LeafletMap
                  lat={shop.latitude}
                  lon={shop.longitude}
                  zoom={16}
                  popupText={shop.name}
                />
              </div>
              <div className="d-none d-lg-block h-100">
                <LeafletMap
                  lat={shop.latitude}
                  lon={shop.longitude}
                  zoom={16}
                  popupText={shop.name}
                  mapHeight={"100%"}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

SectionTitleWithText.propTypes = {
  spaceBottomClass: PropTypes.string,
  spaceTopClass: PropTypes.string,
};

export default SectionTitleWithText;
